<template>
    <div class="main">
        <div class="configurationManagement">
            <div class="searchA">
                <div class="add">
                    <el-button size="small" type="primary" icon="el-icon-plus" @click="addBtn">新增</el-button>
                </div>
            </div>
            <div class="home_table">
                <el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" :max-height="tableHeight" style="width: 100%">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :show-overflow-tooltip="true" :width="item.widthS"></el-table-column>
                    </template>
                    <el-table-column prop="address" label="操作" fixed="right" width="160">
                        <template slot-scope="scope">
                            <el-button @click="editBtn(scope.row)" type="text" size="small">编辑</el-button>
                            <el-button @click="delBtn(scope.row.id)" type="text" size="small" style="color: #ff2b2b">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pagination">
                    <el-pagination background :current-page.sync="pageNum" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="total, sizes, prev, pager, next" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
                </div>
            </div>
            <!-- 新增编辑弹窗 -->
            <el-dialog :title="title" class="dialogStyle" v-dialogDrag :visible.sync="dialogVisible" width="600px" v-if="dialogVisible" :close-on-click-modal="false">
                <div>
                    <el-form :model="formData" ref="formData" :rules="rule" class="demo-ruleForm">
                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item prop="item_name" label="项目名称">
                                        <el-input v-model="formData.item_name" placeholder="请输入项目名称" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item prop="item_key" label="映射值">
                                        <el-input v-model="formData.item_key" placeholder="请输入映射值" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item prop="item_value" label="参数值">
                                        <el-input v-model="formData.item_value" placeholder="请输入参数值" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div class="dialogInput">
                            <div class="padd">
                                <div class="paddTit">项目说明</div>
                                <div>
                                    <el-form-item>
                                        <el-input v-model="formData.remarks" type="textarea" placeholder="请输入项目说明(100个字符以内)" maxlength='100' :autosize="{ minRows: 2, maxRows: 4}"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div style="text-align: center">
                            <el-button type="primary" class="btn" @click="submitForm('formData')">确 定</el-button>
                        </div>
                    </el-form>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import {
    configurationManagementListApi,
    configurationManagementAddApi,
    configurationManagementEditApi,
    configurationManagementDelApi,
    hashApi,
} from '@/api/index';

export default {
    data() {
        return {
            total: 0,
            pageNum: 1,
            pageSize: 15,
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            formData: {
                item_name: '',
                item_key: '',
                item_value: "",
                remarks: ""
            },
            method_auth: {},
            editId: '',
            title: '',
            dialogVisible: false,
            rule: {
                item_name: [
                    { required: true, message: '请输入项目名称', trigger: 'blur' },
                ],
                item_key: [
                    { required: true, message: '请输入映射值', trigger: 'blur' },
                ],
                item_value: [
                    { required: true, message: '请输入参数值', trigger: 'blur' },
                ],
            },
            tableHead: [
                { title: 'ID', prop: 'id', widthS: "100" },
                { title: '项目名称', prop: 'item_name' },
                { title: '映射值', prop: 'item_key' },
                { title: '参数值', prop: 'item_value' },
            ],
            tableData: [],
            hashData: '',
        };
    },
    mounted() {
        this.getListData();
    },
    methods: {
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.pageNum = 1;
            this.pageSize = newSize;
            this.getListData();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.pageNum = newPage;
            this.getListData();
        },
        //获取Hash
        async getHashData() {
            const res = await this.axios.post(hashApi, { page_hash: 'configurationManagement' });
            if (res.data.code == 1) {
                this.hashData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                });
            }
        },
        // 新增编辑提交
        submitForm(formName) {
            this.$refs[formName].validate(async (valid) => {
                if (valid) {
                    let datas = {
                        item_key: this.formData.item_key,
                        item_name: this.formData.item_name,
                        item_value: this.formData.item_value,
                        remarks: this.formData.remarks,
                        hash: this.hashData,
                    };
                    if (this.title == '新增') {
                        var urls = configurationManagementAddApi;
                    } else if (this.title == '编辑') {
                        var urls = configurationManagementEditApi;
                        datas.id = this.editId;
                    }
                    const res = await this.axios.post(urls, datas);
                    if (res.data.code == 1) {
                        this.dialogVisible = false;
                        this.$nextTick(() => {
                            this.getListData();
                        });
                    } else {
                        this.getHashData();
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error',
                        });
                    }
                } else {
                    return false;
                }
            });
        },
        //重置对话框表单
        resetForm() {
            this.formData = {
                item_key: '',
                item_name: '',
                item_value: '',
                remarks: '',
            };
        },
        // 点击新增
        addBtn() {
            this.title = '新增';
            this.dialogVisible = true;
            this.getHashData();
            this.resetForm(); //重置表单
        },
        // 点击编辑
        editBtn(row) {
            this.title = '编辑';
            this.dialogVisible = true;
            this.formData = {
                item_key: row.item_key,
                item_name: row.item_name,
                item_value: row.item_value,
                remarks: row.remarks,
            }
            this.editId = row.id;
            this.getHashData();
            this.$nextTick(() => {
                this.$refs['formData'].clearValidate();
            });
        },

        //删除
        async delBtn(id) {
            this.$confirm('是否删除该条数据?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            })
                .then(async () => {
                    const datas = {
                        id: id,
                    };
                    const res = await this.axios.post(configurationManagementDelApi, datas);
                    if (res.data.code == 1) {
                        this.getListData();
                        this.$message({ showClose: true, message: '删除成功', type: 'success', });
                    } else {
                        this.$message({ showClose: true, message: res.data.msg, type: 'error', });
                    }
                })
                .catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除',
                    });
                });
        },
        //获取列表
        async getListData() {
            this.isLoading = true;
            let datas = {
                curr: this.pageNum,
                pagesize: this.pageSize,
            };
            const { data } = await this.axios.post(configurationManagementListApi, datas);
            if (data.code == 1) {
                this.tableData = data.data.data;
                this.total = data.data.total;
                this.isLoading = false;
            } else {
                this.isLoading = false;
                this.$message({ showClose: true, message: data.msg, type: 'error', });
            }
        },
    },
};
</script>

<style lang="scss" scoped>
.configurationManagement {
    /deep/ .dialogStyle {
        .el-form-item {
            margin-bottom: 20px !important;
        }
        .el-form-item__label {
            line-height: 20px;
        }
        .el-form--label-top .el-form-item__label {
            padding: 0;
        }
        .el-dialog__body {
            padding: 30px 15px 30px 25px;
        }
        .el-input {
            width: 540px;
        }
        .el-textarea {
            width: 540px !important;
        }
        .el-input-number .el-input__inner {
            text-align: left;
        }
    }
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
            //       .el-button{
            //   border: solid 1px #2f39ff;
            //   color: #2f39ff;
            // }
        }
        .searchBtn {
            border: solid 1px #2f39ff;
            color: #2f39ff;
        }
    }

    .dialogInput {
        display: flex;
        margin-bottom: 10px;
        .padd {
            width: 100%;
            margin-right: 20px;
            .paddTit {
                margin-bottom: 10px;
            }
        }
        .el-select {
            width: 100%;
        }
    }
    .btn {
        width: 250px;
        // margin-top: 20px;
    }
}
</style>



